import * as React from 'react';
import { ThemeProvider, createTheme } from "@mui/material";
import Box from '@mui/material/Box';
import { getData, setData as postData } from "./api";
import Palette from "./interfaces/Palette";
import Main from "./pages/main";
import isDev from "./isDevelop";
import initData from "./data.json"; 
import Mode from './interfaces/Mode';
import Navbar from './components/Navbar';
import Button from './interfaces/Button';

let _mode = localStorage.getItem("mode");
!_mode&& (_mode = "dark");

function App() {
  const [data, setData] = React.useState(initData);
  const [mode, setMode] = React.useState(_mode);

  const toggleMode = () => {
    const _mode = mode==="dark"?"light":"dark";
    setMode(_mode);
    localStorage.setItem("mode", _mode);
  }

  const modeTheme = createTheme({ palette: { mode } });

  const _setData = (_data) => {
    postData(_data).then(result => {
      if(result) setData(_data);
    });
  }
  React.useEffect(() => {
    _setData(initData);
  })
  return (
    <ThemeProvider theme={modeTheme}>
      <Box
        sx={{
          bgcolor: 'background.default',
          color: 'text.primary',
          width: '100vw',
          height: '100vh'
        }}
      >
        <Navbar contents={["A", "B", "C", "D", "E", "F", "G", "H"]} color="redA100" 
          mode={mode} toggleMode={toggleMode}
        />
        <div style={{display:"flex", justifyContent: "center"}}>
          <Palette />
        </div>
        <Main />
        <Button />
      </Box>
    </ThemeProvider>
  );
}

export default App;
