const SERVER_DOMAIN = `http://localhost:${3300}`;

const getData = async () => {
    return await fetch(`${SERVER_DOMAIN}/api`, {
        method: "GET",
    }).then(res => res.json())
}
const setData = async (data) => {
    return await fetch(`${SERVER_DOMAIN}/api`, {
        headers: {
            "Content-Type": "application/json",
        },
        method: "POST",
        body: JSON.stringify({
            data
        })
    }).then(res => res.status === 202)
}

export { getData, setData };